import React, { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import apiClient from '../services/apiClient';
import './ToolsList.css';

const ToolsList = ({ onToolSelect }) => {
    const [tools, setTools] = useState([]);
    const [pagination, setPagination] = useState({
        current_page: 1,
        total_pages: 1,
        total_items: 0,
        page_size: 5, // 每页显示5个工具
        has_next: false,
        has_prev: false
    });

    const [loading, setLoading] = useState(false);
    const [refreshing, setRefreshing] = useState(false);
    const [error, setError] = useState(null);

    // 获取工具列表
    const fetchTools = useCallback(async (page = 1, isRefresh = false) => {
        try {
            if (isRefresh) {
                setRefreshing(true);
            } else {
                setLoading(true);
            }
            setError(null);

            const result = await apiClient.getItems(page, pagination.page_size);

            if (page === 1 || isRefresh) {
                // 刷新或第一页，替换数据
                setTools(result.items);
            } else {
                // 加载更多，追加数据
                setTools(prevTools => [...prevTools, ...result.items]);
            }

            setPagination(result.pagination);
        } catch (error) {
            console.error('获取工具列表失败:', error);
            setError(error.message || '获取工具列表失败');

            // 如果是第一次加载失败，使用备用数据
            if (page === 1 && tools.length === 0) {
                const fallbackTools = [
                    {
                        tool_id: 'maps_weather',
                        name: '天气查询',
                        description: '查询指定城市的天气情况',
                        type: 'http',
                        tags: ['weather', 'system']
                    },
                    {
                        tool_id: 'calendar',
                        name: '日程管理',
                        description: '查看和管理日程安排',
                        type: 'http',
                        tags: ['calendar', 'system']
                    }
                ];
                setTools(fallbackTools);
                setPagination({
                    current_page: 1,
                    total_pages: 1,
                    total_items: fallbackTools.length,
                    page_size: pagination.page_size,
                    has_next: false,
                    has_prev: false
                });
            }
        } finally {
            setLoading(false);
            setRefreshing(false);
        }
    }, [pagination.page_size, tools.length]);

    // 下拉刷新
    const handleRefresh = useCallback(async () => {
        await fetchTools(1, true);
    }, [fetchTools]);

    // 加载更多
    const handleLoadMore = useCallback(async () => {
        if (!loading && pagination.has_next) {
            await fetchTools(pagination.current_page + 1);
        }
    }, [loading, pagination.has_next, pagination.current_page, fetchTools]);

    // 初始加载
    useEffect(() => {
        fetchTools(1);
    }, []);

    return (
        <div className="tools-list-container">
            <div className="tools-list-header">
                <h2>可用工具</h2>
                <button
                    className={`refresh-btn ${refreshing ? 'refreshing' : ''}`}
                    onClick={handleRefresh}
                    disabled={refreshing}
                    aria-label="刷新工具列表"
                >
                    <motion.div
                        animate={{ rotate: refreshing ? 360 : 0 }}
                        transition={{ duration: 1, repeat: refreshing ? Infinity : 0, ease: "linear" }}
                    >
                        🔄
                    </motion.div>
                </button>
            </div>

            {error && (
                <motion.div
                    className="error-message"
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                >
                    <span>⚠️ {error}</span>
                    <button onClick={() => handleRefresh()} className="retry-btn">
                        重试
                    </button>
                </motion.div>
            )}

            <div className="tools-list">
                <AnimatePresence mode="popLayout">
                    {tools.map((tool, index) => (
                        <motion.div
                            key={tool.tool_id}
                            className="tool-item"
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -20 }}
                            transition={{ delay: index * 0.1 }}
                            onClick={() => onToolSelect && onToolSelect(tool.tool_id)}
                            whileHover={{ scale: 1.02 }}
                            whileTap={{ scale: 0.98 }}
                        >
                            <div className="tool-content">
                                <h3 className="tool-name">{tool.name}</h3>
                                <p className="tool-description">{tool.description}</p>
                                <div className="tool-meta">
                                    <span className={`tool-type ${tool.type}`}>
                                        {tool.type === 'mcp' ? 'MCP工具' : 'HTTP接口'}
                                    </span>
                                    <div className="tool-tags">
                                        {tool.tags && tool.tags.slice(0, 2).map(tag => (
                                            <span key={tag} className="tool-tag">{tag}</span>
                                        ))}
                                    </div>
                                </div>
                            </div>
                            <div className="tool-action">
                                <span className="arrow">→</span>
                            </div>
                        </motion.div>
                    ))}
                </AnimatePresence>

                {loading && (
                    <motion.div
                        className="loading-item"
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                    >
                        <div className="loading-spinner"></div>
                        <span>加载中...</span>
                    </motion.div>
                )}
            </div>

            {/* 分页控制 */}
            <div className="pagination-controls">
                <div className="pagination-info">
                    <span className="page-info">
                        第 {pagination.current_page} 页 / 共 {pagination.total_pages} 页
                    </span>
                    <span className="total-info">
                        共 {pagination.total_items} 个工具
                    </span>
                </div>

                {pagination.has_next && (
                    <motion.button
                        className="load-more-btn"
                        onClick={handleLoadMore}
                        disabled={loading}
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                    >
                        {loading ? (
                            <>
                                <div className="loading-spinner small"></div>
                                加载中...
                            </>
                        ) : (
                            '加载更多'
                        )}
                    </motion.button>
                )}

                {!pagination.has_next && tools.length > 0 && (
                    <div className="end-message">
                        已显示全部工具
                    </div>
                )}
            </div>
        </div>
    );
};

export default ToolsList; 